
export class User {
    constructor(public email: string,
        public id: string,
        private _token: string,
        private _tokenExpirationDate: Date) { }

    get Token() {
        if (!this._tokenExpirationDate || new Date() > this._tokenExpirationDate) {
            return null;
        }
        return this._token;
    }

    get expirationDate() {
        return this._tokenExpirationDate;
    }

}
